import { useEffect, useState } from "react";
import { Redirect } from "react-router";
import Auth from "../Auth";
import fetch from "../Services/fetch";
import Validator from "../Services/Validator";
import Modal from "./Modal";
import './user.css'
const moment = window.moment;
const $ = window.$;
const toastr = window.toastr;
toastr.options = {
    "debug": false,
    "closeButton": true,
    "preventDuplicates": true,
    "newestOnTop": true,
    "fadeIn": 300,
    "fadeOut": 1000,
    "timeOut": 1000,
    // "extendedTimeOut": 1000
  }
const User = () => {
    
    const [formData, setFormData] = useState({
        address: Auth.userInfo.address || '',
        contactNum: Auth.userInfo.contactNum || '',
        password: '',
        confirmPassword: '',
    });
    const [err, setErr] =  useState({});

    useEffect(()=>{
        
        $(".modal").on('shown.bs.modal', function(){
            $('.modal-backdrop').css({zIndex: 1037});
        });
    },[])


    const logOut = (e) => {
        
        Auth.logout(()=>{
            return <Redirect to="/login"/>
        });
        
    }
    const preview = (evt, url) => {
        const output = document.getElementById(`img-avatar`);
        
        const objUrl = URL.createObjectURL(evt.target.files[0]);
        output.src = objUrl;
        
        // Auth.userInfo.documents[evt.target.name] = url;
        // localStorage.setItem('user', JSON.stringify(Auth.userInfo));
        
        output.onload = function() {
            URL.revokeObjectURL(output.src) // free memory
            // URL.revokeObjectURL(href.href) // free memory
        }
    }

    // const handleChangeAvatar = async (evt) => {
    //     const form = new FormData();
      
    //     form.append("file", evt.target.files[0]);
    //     form.append("name", evt.target.name);
        
    //     // fetch.post(`${process.env.REACT_APP_HOST}/api/contractor/${Auth._id}/documents`, form, 'file')
    //     // .then(res=>{
    //         preview(evt, 'res.data.data');
    //     // })
    //     // .catch(er=>{
    //     //     console.log(er)
    //     // })
    // }
    const handleChange =  (e) => {
        const target = e.target;
        if(target.name === 'avatar'){
            preview(e);
        }
        setFormData({...formData, [target.name]: target.value});
    }

    const validate = (e) => {
        const name = e.target.name;
        const message = Validator(name, e.target.value);
        
        
        // set error message
        if(message){
            setErr({...err, [e.target.name] : message});
        }
        else{
            delete err[e.target.name];
            setErr({...err});
        }

        if(name === 'confirmPassword') {
            if(formData.password !== e.target.value){
                setErr({...err, [e.target.name] : 'Password does not match'});
            }else{
                delete err[e.target.name];
                setErr({...err});
            }
        }
        
    }

    const updateProfile = async () => {
        const data = formData;

        // console.log((Object.values(err)).length > 0, Object.values(err))
        // check if there are errors
        const errors = (Object.values(err));
        if(errors.length > 0)
        return toastr.error(errors[0]);
        
        // check if password match
        if(data.password !== data.confirmPassword)
        return setErr({...err, confirmPassword: 'password does not match'});

        // check if password has been changed
        if(!data.password)
        delete data.password;

        delete data.confirmPassword;
        
        const authuser = await fetch.post(`${process.env.REACT_APP_HOST}/api/user/${Auth._id}/update`, data)
        if(authuser) {
            toastr.success('Successfully updated.');
            const userInfo = data;
            delete userInfo.password;

            // update user info
            Auth.update(userInfo);

            setFormData({...data, password:'', confirmPassword: ''});
        }
        $('#userProfileModal').modal('hide')
    }

    return (<>
        <div className="user-panel pt-3 pb-3 mb-3 d-flex align-items-center" >
            <div className="image">
                {/* <img src="dist/img/user-ramir.png" className="img-circle elevation-2" alt="User" /> */}
                <a href="#/" data-toggle="modal" data-target="#userProfileModal">
                    {/* <i className="fas fa-user-circle" style={{color: 'white', fontSize: '3em'}}></i> */}
                    <img className="profile-user-img img-fluid img-circle" src={Auth.userInfo.avatar? Auth.userInfo.avatar : "../../dist/img/user1-128x128.jpg"} alt="User profile" />
                </a>
            </div>
            <div className="info brand-text">
                <a href="#/" className="d-block">{Auth.user}</a>
                <a href="#/" className="d-block" onClick={logOut}>Logout</a>
            </div>
        </div>

         {/* <!-- Modal --> */}
         <Modal title="User Profile" size="modal-md" id="userProfileModal" headerClass="card-outline card-primary">
            <div className="card-body row">
            <div className="col-lg-12">
                <div className="card">
                    <div className="row p-2 align-items-center bg-primary">
                        <div className="col-3 text-left">
                            <label htmlFor="avatar">
                                <img id="img-avatar" className="cursor-pointer profile-user-img img-fluid img-circle" src={Auth.userInfo.avatar? Auth.userInfo.avatar : "../../dist/img/user1-128x128.jpg"} alt="User profile" />
                                {/* <i className="fas fa-user-circle"></i> */}
                            </label>
                            <input type="file" name="avatar" onChange={handleChange} accept="image/*" id="avatar" className="d-none"/>
                        </div>
                        <div className="col-9">
                            <div>
                                <label>Name: {Auth.user}</label>
                            </div>
                            <div>
                                <label>Date Registered: {moment(Auth.userInfo.created_at).format('MMM Do, YYYY')}</label>
                            </div>
                        </div>
                    </div>
                </div>
                <div className="card">
                    <div className="card-header p-2">
                        <ul className="nav nav-pills">
                        <li className="nav-item"><a className="nav-link profile-nav-link active" href="#about" data-toggle="tab">Basic-Info</a></li>
                        <li className="nav-item"><a className="nav-link profile-nav-link" href="#settings" data-toggle="tab">Security</a></li>
                        </ul>
                    </div>
                {/* <!-- /.card-header --> */}
                <div className="card-body">
                    <div className="tab-content">
                        <div className="tab-pane active" id="about">
                            <div className="form-group">
                                <label htmlFor="email" className="col-form-label">Email</label>
                                <input type="email" value={Auth.userInfo.email} readOnly name="email" className="form-control" id="email" placeholder="Email Address" />
                            </div>
                            <div className="form-group">
                                <label htmlFor="address"  className="col-form-label">Address</label>
                                <input type="text" onChange={handleChange} value={formData.address} onBlur={validate}
                                name="address" className="form-control" id="address" placeholder="Address" />
                                {err.address && <small className="text-danger">{err.address || ''}</small> }
                            </div>
                            <div className="form-group">
                                <label htmlFor="contact" className="col-form-label">Contact Number</label>
                                <input type="text" onChange={handleChange} value={formData.contactNum} onBlur={validate}
                                name="contactNum" className="form-control" id="contact" placeholder="Contact Number" />
                                {err.contactNum && <small className="text-danger">{err.contactNum || ''}</small> }
                            </div>
                        </div>
                        {/* <!-- /.tab-pane --> */}
                        <div className="tab-pane" id="settings">
                            <form className="form-horizontal">
                                <div className="form-group">
                                    <label htmlFor="password" className="col-form-label">New Password</label>
                                    <input type="text" onChange={handleChange} value={formData.password} onBlur={validate}
                                    name="password" className="form-control" id="password" placeholder="Name" />
                                    {err.password && <small className="text-danger">{err.password || ''}</small> }
                                </div>
                                <div className="form-group">
                                    <label htmlFor="password" className="col-form-label">Confirm Password</label>
                                    <input type="text" onChange={handleChange} value={formData.confirmPassword} onBlur={validate}
                                    name="confirmPassword" className="form-control" id="password" placeholder="Name" />
                                    {err.confirmPassword && <small className="text-danger">{err.confirmPassword || ''}</small> }
                                </div>
                            </form>
                        </div>
                        {/* <!-- /.tab-pane --> */}

                    </div>
                        {/* <!-- /.tab-content --> */}
                </div>
                {/* <!-- /.card-body --> */}
                </div>
            </div>
            </div>
            {/* <!-- /.card-body --> */}
            <div className="modal-footer card-footer text-right">
                <button type="button" className="btn btn-secondary" data-dismiss="modal">Close</button>
                <button type="button" className="btn btn-primary" onClick={updateProfile}>Update</button>
            </div>
        </Modal>
    </>);
}
 
export default User;